import { mapGetters, mapActions } from "vuex";
import centerList from "@/component/center-list.vue";
import pageHead from "@/component/page-header.vue";
import userAPI from "@/api/user.js";

export default {
  components: {
    centerList,
    pageHead
  },
  name: "center",
  data() {
    return {
      userInfo: null,
      Info: {
        name: "无名氏",
        phone: "131xxxx4382",
        pic: "./images/personal_image_avatar_small@2x.png"
      }
    };
  },
  methods: {
    ...mapActions(["setUser"]),
    logout: function() {
      userAPI.logout();
      this.setUser(null);
      this.$router.push("/");
    }
  },
  watch: {},
  computed: {
    ...mapGetters(["user"])
  },
  created() {},
  mounted() {
    let user = this.user;
    if (user != null) {
      this.userInfo = user;
      this.Info.name = user.nickName;
      this.Info.phone = user.mobile;
      this.Info.pic = "/service/rest/tk.File/" + user.avatar;
    }
    let elem = document.querySelector("#vux_view_box_body");
    setTimeout(() => {
      elem.style.paddingBottom = "0";
    }, 10);
  }
};
